import React from 'react';
import { useNavigate } from 'react-router-dom';

const None = () => {
  const navigate = useNavigate();

  return (
    <div>
      <h1>해당 정보만으로는 촉감을 예측할 수 없습니다</h1>
      <button onClick={() => navigate('/')}>홈으로 돌아가기</button>
    </div>
  );
};

export default None;
